import { asyncComponent } from 'nc-lightapp-front';
import list from '../list';

/**
* @description: 
*  webpackChunkName:///...
*/
const card = asyncComponent(() =>
        import(/*webpackChunkName: "fmc/fmc/cost/card/card"*/ /* webpackMode: "eager" */ '../card')
);

const routes = [
        {
                path: '/',
                component: list,
                exact: true
        },
        {
                path: '/list',
                component: list
        },
        {
                path: '/card',
                component: card
        }
];

export default routes;
